$font-body: 'Josefin Sans', sans-serif;
$bg-body: #ffffff;
$primary-color: #5da88a;
$secondary-color: #451d1d;
$white-color: #ffffff;
$body-color: #1a1a1a;
$body-text: $body-color;
$grid-breakpoints: (
  xs: 1px,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
);
$spacer: 1rem;
$spacers: (
  0: 0,
  1: $spacer * .25,
  2: $spacer * .5,
  3: $spacer,
  4: $spacer * 1.5,
  5: $spacer * 3,
);